.TopWave {
  --width: 969px;
  --height: 20px;
  top: calc(var(--height) * -1);
  height: var(--height);
  background-image: url("top-wave.b61b5c1c.svg");
  background-position: 0 0;
  background-size: calc(var(--width) * 4) var(--height);
  will-change: transform, background-position;
  background-repeat: repeat;
  transition: -webkit-transform 1s, transform 1s;
  -webkit-animation: TopWave 10s linear infinite;
  animation: TopWave 10s linear infinite;
  position: absolute;
  left: 0;
  right: 0;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.TopWave.-active {
  -webkit-transform: translate3d(0, var(--height), 0);
  transform: translate3d(0, var(--height), 0);
}

.TopWave.-pause {
  -webkit-animation-play-state: paused;
  animation-play-state: paused;
}

@-webkit-keyframes TopWave {
  to {
    background-position: calc(var(--width) * 4) 0;
  }
}

@keyframes TopWave {
  to {
    background-position: calc(var(--width) * 4) 0;
  }
}

